@import url('https://fonts.googleapis.com/css2?family=Coda+Caption:wght@800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Chau+Philomene+One&display=swap');
* {
  box-sizing: border-box;
  padding: 0;
}

body {
  background:url("./bg4.jpg");
  background-size: cover;
  padding-bottom: 50px;
}

.close {
  color: orangered;
  float: right;
  display: flex;
  align-items: center;
  margin: 20px;
}

.title-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.title {
  font-family: 'Coda Caption', sans-serif;
  font-size: 50px;
  color: black;
}

.search-btn {
  min-width: 250px;
  background-color: #b9e5d8;
  color: rgb(29, 27, 27);
  border-radius: 25px;
  display:flex;
  align-items: center;
  padding-left:10px;
  padding-right: 20px;
}
.news-row {
  background-color: #8bd3bf3d;
  min-height: 500px;
  width: 100%;
  border-radius: 20px;
  border: 1px solid #14d39d;
}

.label {
  color: white;
  background-color: #8bd3bf;
  padding: 20px;
  text-align: center;
  margin-top: 0px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.feeds {
  background-color: #fff;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.feeds .feed-title{
  font-family: 'Chau Philomene One', sans-serif;
}
/* .feeds .image{
  text-align: center;
} */
.feeds .image img {
  margin: 10px;
}

.time-stamp {
  color: #053fddb4;
  background: #8bebfc38;
  text-align: center;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  font-family: cursive;
}

.Author{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 700;
  color:rgb(3, 3, 80);
}
.Author span{
  color:orange;
  font-style: italic;
}
.popup-content {
  margin: auto;
  background: rgb(255, 255, 255);
  width: 50%;
  padding: 5px;
}
.popup-arrow {
  color: rgb(255, 255, 255);
}
[role='tooltip'].popup-content {
  width: 200px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
}

.popup-overlay {
  background: rgba(0, 0, 0, 0.5);
}
[data-popup='tooltip'].popup-overlay {
  background: transparent;
}
@media screen and (max-width:600px) {
  .Author{
      display:none;
  }
}